import React, { Component } from 'react';
import './convertor.css'

const INITIAL_STATE = {
    left_select: 'CAD',
    right_select:'CAD',
    left_input:'',
    right_input:''
  };


export class Convertor extends Component {
    state = {
        ...INITIAL_STATE,
        data: undefined
    };

    componentDidMount() {
        fetch('https://api.exchangeratesapi.io/latest?base=CAD')
            .then(r => r.json())
            .then(d=> {
                this.setState({ data: d.rates})
            })
            
    }
    onChangeRightSelect = event =>{
        this.setState({right_select: event.target.value})
        this.setState({right_input: this.state.left_input*this.state.data[event.target.value]})
    }

    onChangeLeftSelect = event =>{
    fetch('https://api.exchangeratesapi.io/latest?base=' + event.target.value)
            .then(r => r.json())
            .then(d => {
                this.setState({ data: d.rates })
                this.setState({right_input: this.state.left_input*d.rates[this.state.right_select]})
            }
            );
            this.setState({left_select: event.target.value})
            
}  
onChangeLeftInput = event =>{
    this.setState({left_input: event.target.value})
    !isNaN(event.target.value)?this.setState({right_input: event.target.value*this.state.data[this.state.right_select]})
    :this.setState({right_input: "Неверный формат ввода"})
}

    render() {
        const{right_input} = this.state
        return (
            <div className="convert_page">  
                <div class="rates_main_text">Конвертор</div>  
                <div class = "convertor_box">
                
                    <input type="text" className="input_box" onChange={this.onChangeLeftInput} pattern="[0-9]"/>
                    <select  className="select_box" onChange={this.onChangeLeftSelect}>
                                {
                                    !this.state.data
                                        ? <div>Данные не загружены</div>
                                        : Object.keys(this.state.data).map(key=><option value = {key}>{key}</option>)
                                }
                            </select>
                            <input type="text" className="input_box" value={right_input}/>
                            <select className="select_box" onChange={this.onChangeRightSelect}>
                                {
                                    !this.state.data
                                        ? <div>Данные не загружены</div>
                                        : Object.keys(this.state.data).map(key=><option value = {key}>{key}</option>)
                                }
                            </select>
                            
                </div>
            </div>
        );
    }
}
