import React from 'react'
import './signup.css'
import Firebase from '../../components/firebase/firebase'

const fire = new Firebase();

const INITIAL_STATE = {
  username: '',
  email: '',
  FIO: '',
  user_password: '',
  file: '',
  isLogged: false,
  isError: false
};

export class SignUp extends React.Component{
    
  constructor(props) {
    super(props);
 
    this.state = { ...INITIAL_STATE };
  }

  setError = () =>{
    this.setState({isError: true, username: '', user_password: ''});
    document.getElementById("signup_basis").style.boxShadow="rgba(0, 0, 0, 0.3) 0px 1px 4px, rgba(255, 0, 0, 0.8) -23px 0px 40px -23px, rgba(255, 0, 0, 0.8) 23px 0px 40px -23px, rgba(0, 0, 0, 0.1) 0px 0px 40px inset";
  }
  
  errorMsg = () => {
    if (this.state.isError)
    return(
      <div class ="error_msg">
       <div class="error_text">Логин уже занят</div>
       </div>
    )
  }

    render(){

      const {
        username,
        email,
        FIO,
        user_password
      } = this.state; 
      
      const isInvalid = user_password === '' || email === '' || username === '';
        return(
          
        <div id = "signup_basis">
            <div class="reg_title">Регистрация</div>
            {this.errorMsg()}
            <form class = "submit_form" onSubmit={this.onSubmit}>
            <input class="line" name="username" value={username} onChange={this.onChange} required type="text" placeholder="Логин*" pattern="[A-Za-z0-9]+"/>
            <input class="line" name="email" value={email} onChange={this.onChange} type="email" required placeholder="Email*" pattern="[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,}$"/>
            <div class="Info">Формат ввода:__@__.__</div>
            <input class="line" name="FIO" value={FIO} onChange={this.onChange} type="text" placeholder="ФИО" pattern="[a-zA-Zа-яА-Я\s]+"/>
            <div class="Info">Строчные и прописные буквы русского и латинского алфавита</div>
            <input class="line" name="user_password" value={user_password} onChange={this.onChange} type="text" required placeholder="Пароль*" />
            <input class="line" id="file_name" type="file" required accept="image/png, image/jpeg"></input>
            <button class = "sub_button" disabled={isInvalid} type = "submit">Зарегистрироваться</button>          
        </form>
        </div>
        );
    }

    onChange = event => {
      this.setState({ [event.target.name]: event.target.value });
    };

    onSubmit = event => {
      const { username, email, FIO, user_password } = this.state;
      const file = document.getElementById('file_name').files[0];
        fire.doSingUp(username, email, FIO, user_password, file, this.props.LoggedIn, this.setError)
        
        event.preventDefault();
    }   
  }