import React from 'react';
import './signin.css'
import Firebase from '../../components/firebase/firebase'
import {
  useHistory,
  useLocation
} from "react-router-dom";

const fire = new Firebase();

const INITIAL_STATE = {
    username: '',
    user_password: ''
  }; 



export class SignIn extends React.Component{
constructor(props){
    super(props);
    this.state = { ...INITIAL_STATE,  IsError: false };
}

setError = () =>{
  this.setState({isError: true, username: '', user_password: ''});
  document.getElementById("signin_basis").style.boxShadow="rgba(0, 0, 0, 0.3) 0px 1px 4px, rgba(255, 0, 0, 0.8) -23px 0px 40px -23px, rgba(255, 0, 0, 0.8) 23px 0px 40px -23px, rgba(0, 0, 0, 0.1) 0px 0px 40px inset";
}

errorMsg = () => {
  if (this.state.isError)
  return(
    <div class ="error_msg">
     <div class="error_text">Ошибка ввода</div>
     <div class="error_text">Неверный пользователь или пароль</div>
     </div>
  )
}

    render(){
        const {
            username,
            user_password
          } = this.state; 
          
          const isInvalid = user_password === '' || username === '';
        return(
        <div id = "signin_basis">
            <div class="login_title">Вход</div>
            {this.errorMsg()}
            <form class = "submit_form" onSubmit={this.onSubmit}>
            <input class="line" name="username" value={username} onChange={this.onChange} type="text" placeholder="Логин" pattern="[A-Za-z0-9]+"/>
            <input class="line"  name="user_password" value={user_password} onChange={this.onChange} type="text" placeholder="Пароль" />
            <button class = "sub_button" type = "submit">Войти</button>          
        </form>
        </div>
        );
    }


    onChange = event => {
        this.setState({ [event.target.name]: event.target.value });
      };
  
      onSubmit = event => {
        event.preventDefault();
        const { username, user_password } = this.state;
          fire.doLogIn(username,user_password,this.props.LoggedIn,this.setError);           
      }   
}